.main-header {
	/* background-color: var(--header-background-color); */

	display: flex;
	flex-direction: column;
	position: sticky;
	z-index: 5;
	top: 0;
	background-color: var(--header-background-color);
	width: 100vw;
	/* overflow: hidden; */

	.header1 {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100vw;
		height: fit-content;
		border-top: 0.03rem solid var(--primary-color);
		border-bottom: 0.03rem solid var(--primary-color);
		p {
			font-size: calc(8px + 0.5vw);
			margin: 0 3vw;
			text-align: center;
		}
	}

	.header {
		display: flex;
		justify-content: space-between;
		/* padding: 0.3rem calc(2vw + 1vh); */
		padding: 0 calc(2vw + 1vh);
		border-bottom: 0.03rem solid var(--primary-color);

		.logo-container {
			display: flex;
			align-items: center;
			padding: 1vw 0.5vw;
			padding: 0 0.5vw;
			border-radius: 5px;
			img {
				height: calc(5px + 3vw);
				object-fit: cover;
				border-radius: 50%;
			}
			.text {
				margin: 0 1vw;
				font-size: calc(8px + 0.5vw);
			}
			&:hover {
				background-color: var(--primary-color);
				color: var(--font-primary-color);
			}
		}
		.nav-options-container {
			display: flex;
			width: 85vw;
			justify-content: space-between;
			.header-menu {
				flex: 1;
				display: flex;
				justify-content: flex-end;
				.nav-option {
					align-self: center;
					padding: 0.8vw 0.7vw;
					cursor: pointer;
					border-radius: 5px;
					font-size: calc(8px + 0.5vw);
					span {
						white-space: nowrap;
					}

					&:hover {
						background-color: var(--primary-color);
						color: var(--font-primary-color);
					}
				}
			}
			.menu-button-icon {
				align-self: center;
				padding: 0.7vw 0.5vw;
				border-radius: 5px;
				cursor: pointer;
				display: none;

				img {
					height: calc(5px + 1vw);
					object-fit: cover;
				}
				&:hover {
					background-color: var(--primary-color);
					color: var(--font-primary-color);
				}
			}
			.sign-in {
				font-size: calc(8px + 0.5vw);
				align-self: center;
				padding: 0.8vw 0.5vw;
				border-radius: 5px;
				white-space: nowrap;
				cursor: pointer;
				&:hover {
					background-color: var(--primary-color);
					color: var(--font-primary-color);
				}
			}
			.user-info-container {
				display: flex;
				align-self: center;
				padding: 0.3vw 0.5vw;
				border-radius: 5px;
				cursor: pointer;
				img {
					height: calc(20px + 1.4vw);
					object-fit: cover;
					border-radius: 50%;
					margin: 0 0.5vw;
				}
				&:hover {
					background-color: var(--primary-color);
					color: var(--font-primary-color);
				}
				.text {
					font-size: calc(8px + 0.5vw);
					margin: 0.5vw;
				}
			}

			.menu-item-dropdown {
				&:hover {
					.nav__submenu {
						display: block;
					}
				}
			}

			.nav__submenu {
				display: none;
				position: absolute;
				top: 10.7vh;
				width: 12vw;
				padding: 2vh;
				background-color: var(--primary-color-m);
				z-index: 5;

				.drop {
					color: var(--font-primary-color);
					display: block;
					border: 0.1rem solid var(--primary-color);
					text-align: center;
					font-size: calc(8px + 0.5vw);
					white-space: nowrap;
					&:hover {
						background-color: var(--primary-color);
						color: var(--font-secondary-color);
					}
				}
			}
		}
	}
}
@media only screen and (max-width: 700px) {
	#root {
		.main-container {
			.main-header {
				.header {
					.nav-options-container {
						width: 20vw;
						.header-menu {
							flex-direction: column;
							/* position: absolute;
							top: 10vh;
							background-color: white;
							width: 10vw; */

							position: absolute;
							top: 10.7vh;
							width: 12vw;
							padding: 2vh;
							background-color: var(--primary-color-m);
							z-index: 5;
							.nav-option {
								/* padding: 0 5vw;
								border: 0.03rem solid var(--primary-color); */
								color: var(--font-primary-color);
								display: block;
								/* border: 0.1rem solid var(--primary-color); */
								text-align: center;
							}
							:hover {
								background-color: var(--primary-color);
								color: var(--font-secondary-color);
							}
						}
						.menu-button-icon {
							display: block;
						}
					}
				}
			}
		}
		background-color: rgb(253, 253, 253);
	}
}
